import React, { useState } from "react";
import { Menu, Form, Input, Button, Descriptions, message, Modal } from "antd";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import PageHeader from "../../components/pageHeader";
import $axios from "@/axios/$axios";
import { isMobile } from "react-device-detect";

const { Item } = Menu;
const formItemLayoutWithOutLabel = {
  wrapperCol: {
    xs: { span: 24, offset: 0 },
    sm: { span: 16, offset: 8 },
  },
};
const formItemLayout = {
  labelCol: {
    sm: { span: 8 },
  },
  wrapperCol: {
    sm: { span: 12, offset: 0 },
  },
};
const menuMap = {
  info: "",
  changeEmail: "",
  changePassword: "",
  // notification: "New Message Notification"
};
const AccountPage = (props) => {
  const [loading, setLoading] = useState(false);
  const [selectedKey, setSelectedKey] = useState("info");
  const info = () => {
    Modal.info({
      title: "",
      content: (
        <div>
          <p> Github </p>
        </div>
      ),
      onOk: () => {
        setLoading(false);
      },
    });
  };

  const onFinish = (values) => {
    setLoading(true);
    $axios
      .post(`/user/update/${props.user._id}`, values)
      .then(() => {
        message.success("");
        setLoading(false);
      })
      .catch(() => {
        message.error("");
        setLoading(false);
      });
  };
  const checkUpdate = async () => {
    setLoading(true);

    await $axios
      .get("/setting")
      .then((result) => {
        if (result.data.version > props.setting.version) {
          info();
        } else {
          message.success("");
          setLoading(false);
        }
      })
      .catch(() => {
        message.error("");
        setLoading(false);
      });
  };
  const getMenu = () => {
    return Object.keys(menuMap).map((item) => (
      <Item key={item}>{menuMap[item]}</Item>
    ));
  };
  const selectKey = (key) => {
    setSelectedKey(key);
  };
  const renderAccountInfo = () => {
    return (
      <Descriptions title="" style={{ padding: 20 }}>
        <Descriptions.Item label="">{props.user.email}</Descriptions.Item>
        <Descriptions.Item label="">
          {props.user.date}
        </Descriptions.Item>
        <Descriptions.Item label="">
          {props.setting.version}
        </Descriptions.Item>
        <Descriptions.Item label="">
          <Button type="primary" onClick={checkUpdate} loading={loading}>
            
          </Button>
          <a
            href="https://github.com/troyeguo/coodo-pay"
            target="_blank"
            rel="noopener noreferrer"
          >
            &nbsp;&nbsp;&nbsp;
          </a>
        </Descriptions.Item>
      </Descriptions>
    );
  };
  const renderChangeEmail = () => {
    return (
      <Form
        {...formItemLayout}
        onFinish={onFinish}
        initialValues={props.formData ? props.formData : null}
        style={{ marginTop: "40px" }}
      >
        <Form.Item
          label=""
          name="email"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>

        <Form.Item
          label="1"
          name="answer1"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label="2"
          name="answer2"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item {...formItemLayoutWithOutLabel}>
          <Button type="primary" htmlType="submit" loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    );
  };
  const renderChangePassword = () => {
    return (
      <Form
        {...formItemLayout}
        onFinish={onFinish}
        initialValues={props.formData ? props.formData : null}
        style={{ marginTop: "40px" }}
      >
        <Form.Item
          label=""
          name="password"
          rules={[
            { min: 8, message: "8" },
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input.Password placeholder="" />
        </Form.Item>

        <Form.Item
          label="1"
          name="answer1"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label="2"
          name="answer2"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item {...formItemLayoutWithOutLabel}>
          <Button type="primary" htmlType="submit" loading={loading}>
            
          </Button>
        </Form.Item>
      </Form>
    );
  };

  const renderChildren = () => {
    switch (selectedKey) {
      case "info":
        return renderAccountInfo();

      case "changeEmail":
        return renderChangeEmail();
      case "changePassword":
        return renderChangePassword();
      default:
        break;
    }

    return null;
  };
  return (
    <div className="product-page-container" style={{ position: "relative" }}>
      <PageHeader title="" desc="" />
      <div
        className="main"
        style={
          isMobile
            ? { margin: "5px", height: "100%", position: "relative" }
            : { margin: "20px", height: "100%", position: "relative" }
        }
      >
        <div className={"leftMenu"} style={isMobile ? { width: "100%" } : {}}>
          <Menu
            mode="inline"
            selectedKeys={[selectedKey]}
            onClick={({ key }) => selectKey(key)}
            className="payment-page"
            style={{
              marginTop: "10px",
            }}
          >
            {getMenu()}
          </Menu>
        </div>
        <div
          className="right"
          style={isMobile ? { padding: 0 } : { marginLeft: "50px" }}
        >
          {renderChildren()}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    user: state.form.user,
    setting: state.product.setting,
  };
};
const actionCreator = {};
export default connect(mapStateToProps, actionCreator)(withRouter(AccountPage));
