import React, { useState } from "react";
import { Card, List, message } from "antd";
import "./index.css";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import $axios from "@/axios/$axios";
import PageHeader from "../../components/pageHeader";
import { isMobile } from "react-device-detect";
import { CheckOutlined, LoadingOutlined } from "@ant-design/icons";
const ThemePage = (props) => {
  const [loading, setLoading] = useState(false);
  const [themeIndex, setThemeIndex] = useState(-1);
  const handleApply = (theme, index) => {
    setLoading(true);
    setThemeIndex(index);
    $axios
      .post(`/setting/${props.setting._id}`, {
        ...props.setting,
        themeOption: theme,
      })
      .then(() => {
        setLoading(false);
        setThemeIndex(-1);
        message.success("");
      })
      .catch(() => {
        setLoading(false);
        setThemeIndex(-1);

        message.error("");
      });
  };
  const cardList = (
    <List
      rowKey="id"
      // loading={loading}
      grid={{
        gutter: 24,
        xl: 4,
        lg: 3,
        md: 3,
        sm: 2,
        xs: 1,
      }}
      dataSource={[
        {
          id: "default",
          title: "",
          cover: "/assets/default.svg",
          href: "",
          subDescription: "",
        },
        {
          id: "tech",
          title: "",
          cover: "/assets/tech.svg",
          href: "",
          subDescription: "",
        },
        {
          id: "nostalgic",
          title: "",
          cover: "/assets/nostalgic.svg",
          href: "",
          subDescription: "",
        },
        {
          id: "blur",
          title: "",
          cover: "/assets/blur.svg",
          href: "",
          subDescription: "",
        },
      ]}
      renderItem={(item, index) => (
        <List.Item>
          <Card
            className={"theme-card"}
            hoverable
            cover={
              <img
                alt={item.title}
                src={item.cover}
                className="theme-card-cover"
              />
            }
            actions={[
              <div
                key="apply"
                onClick={() => {
                  handleApply(item.id, index);
                }}
              >
                {loading && index === themeIndex ? (
                  <LoadingOutlined />
                ) : (
                  <CheckOutlined />
                )}
                &nbsp; 
              </div>,
            ]}
          >
            <Card.Meta title={item.title} description={item.subDescription} />
          </Card>
        </List.Item>
      )}
    />
  );

  return (
    <div className="product-page-container" style={{ position: "relative" }}>
      <PageHeader title="" desc="" />
      <div
        className={"coverCardList"}
        style={isMobile ? { margin: "5px" } : { margin: "20px" }}
      >
        <div
          className={"cardList"}
          style={isMobile ? { margin: "5px" } : { margin: "20px 0px" }}
        >
          {cardList}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    setting: state.product.setting,
  };
};
const actionCreator = {};
export default connect(mapStateToProps, actionCreator)(withRouter(ThemePage));
