import React, { useState, useEffect } from "react";
import { Table, Button, Input, DatePicker, Badge, message } from "antd";
import moment from "moment";
import { connect } from "react-redux";
import $axios from "../../axios/$axios";
import { handleFetchDisaccount } from "@/redux/actions/form";
import PageHeader from "../../components/pageHeader";
import { isMobile } from "react-device-detect";
import CreateDisaccount from "../../components/createDisaccount";

const dateFormat = "YYYY-MM-DD";
const { Search } = Input;
const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 },
};
const tailLayout = {
  wrapperCol: {
    xs: { span: 16, offset: 0 },
    sm: { span: 16, offset: 10 },
  },
};
const DisaccountPage = (props) => {
  const [data, setData] = useState(props.disaccount);
  const [loading, setLoading] = useState(true);
  const [deleteLoading, setDeleteLoading] = useState(false);
  const [deleteIndex, setDeleteIndex] = useState(-1);
  const [title, setTile] = useState("");
  const [editDisaccount, setEditDisaccount] = useState(null);
  const [selectedRowKeys, setSelectedRowKeys] = useState([]);
  const [isShowCreate, setShowCreate] = useState(false);
  useEffect(() => {
    if (props.disaccount) {
      setLoading(false);
      setData(props.disaccount);
    }
  }, [props.disaccount]);
  const filterData = (date = {}) => {
    let filteredData = [];
    props.disaccount.forEach((item) => {
      if (
        item.year === date.year &&
        item.month === date.month &&
        item.day === date.day
      ) {
        filteredData.push(item);
      }
    });
    setData(filteredData);
  };

  const onSelectedRowKeysChange = (selectedRowKeys) => {
    setSelectedRowKeys(selectedRowKeys);
  };
  const handleSearch = (value) => {
    let searchResults = [];
    props.disaccount.forEach((item) => {
      if (item.code === value) {
        searchResults.push(item);
      }
    });
    setData(searchResults);
  };
  const handleReset = () => {
    setData(props.disaccount);
    let inputBox = document.querySelector(".ant-input");
    inputBox.value = "";
  };
  const onDateChange = (date) => {
    date &&
      filterData({
        year: date._d.getFullYear(),
        month: date._d.getMonth() + 1,
        day: date._d.getDate(),
      });
  };

  const handleDelete = async (code, index, disaccount) => {
    setDeleteLoading(true);
    setDeleteIndex(index);
    $axios
      .post(`/disaccount/delete/${disaccount._id}`)
      .then((res) => {
        setDeleteLoading(false);
        message.success("");
        props.handleFetchDisaccount();
        setDeleteIndex(-1);
      })
      .catch((err) => {
        setDeleteLoading(false);
        setDeleteIndex(-1);
        message.error("");
      });
  };

  const rowSelection = {
    selectedRowKeys,
    onChange: onSelectedRowKeysChange,
  };
  const columns = [
    {
      dataIndex: "code",
      key: "code",
      width: 80,
      render: (text, record, index) => (
        <Button
          onClick={() => {
            setTile("");
            setEditDisaccount(record);
            setShowCreate(true);
          }}
          size="small"
        >
          
        </Button>
      ),
    },
    {
      dataIndex: "code",
      key: "code",
      width: 80,
      render: (text, record, index) => (
        <Button
          type="primary"
          onClick={() => {
            handleDelete(record.code, index, record);
          }}
          size="small"
          loading={deleteIndex === index && deleteLoading}
        >
          
        </Button>
      ),
    },
    {
      title: "",
      key: "code",
      dataIndex: "code",
      width: 200,
    },
    {
      title: "",
      key: "productName",
      dataIndex: "productName",
      width: 120,
      render: (productName) => (
        <p>{productName === "all" ? "" : productName}</p>
      ),
    },
    {
      title: "",
      key: "levelName",
      dataIndex: "levelName",
      width: 100,
      render: (levelName) => (
        <p>{levelName === "all" ? "" : levelName}</p>
      ),
    },
    {
      title: "",
      key: "activation",
      dataIndex: "activation",
      width: 100,
      render: (activation) =>
        activation.length > 0 ? (
          <Badge status="success" text={""} />
        ) : (
          <Badge status="warning" text={""} />
        ),
    },
    {
      title: "",
      key: "activation",
      dataIndex: "activation",
      width: 100,
      render: (activation) => (
        <p style={{ textAlign: "center" }}>{activation.length}</p>
      ),
    },
    {
      title: "",
      key: "number",
      dataIndex: "number",
      width: 100,
      render: (number) => <p style={{ textAlign: "center" }}>{number}</p>,
    },
    {
      title: "",
      dataIndex: "time",
      key: "time",
      width: 140,
      render: (time) => new Date(parseInt(time)).toLocaleDateString(),
    },
    {
      title: "",
      dataIndex: "validUntil",
      key: "validUntil",
      width: 140,
      render: (validUntil) => new Date(validUntil).toLocaleDateString(),
    },
    {
      title: "",
      dataIndex: "amount",
      key: "amount",
      width: 100,
      render: (text, record, index) => (
        <span>
          {record.amount}
          {record.amountType === "price" ? "" : "%"}
        </span>
      ),
    },
    {
      title: "",
      dataIndex: "disaccountType",
      key: "disaccountType",
      width: 100,
      render: (disaccountType) =>
        disaccountType === "one_time" ? (
          <span></span>
        ) : (
          <span></span>
        ),
    },
  ];
  const date = new Date();
  return (
    <div className="shadow-radius">
      {isShowCreate && (
        <CreateDisaccount
          isShowCreate={isShowCreate}
          setShowCreate={setShowCreate}
          title={title}
          disaccountInfo={editDisaccount}
          setEditDisaccount={setEditDisaccount}
        />
      )}

      <PageHeader title="" desc="" />
      <div
        style={
          isMobile
            ? {
                backgroundColor: "white",
                height: "170px",
                margin: "5px",
                zIndex: 10,
              }
            : {
                backgroundColor: "white",
                margin: "20px 20px 0 20px",
              }
        }
      >
        <Search
          placeholder=""
          enterButton=""
          style={
            isMobile
              ? {
                  width: 240,
                  margin: "25px 10px 5px 10px",
                  height: "20px",
                  fontSize: "25px",
                }
              : {
                  width: 300,
                  margin: "25px 10px 5px 10px",
                  height: "20px",
                  fontSize: "25px",
                }
          }
          onSearch={(value) => {
            handleSearch(value);
          }}
        />
        <DatePicker
          defaultValue={moment(`${date.toLocaleDateString()}`, dateFormat)}
          format={dateFormat}
          onChange={onDateChange}
          style={{
            width: 240,
            margin: "25px 10px 5px",
          }}
        />
        <Button
          onClick={handleReset}
          style={
            isMobile
              ? {
                  margin: "5px 10px",
                  color: "#40A9FF !important",
                }
              : {
                  margin: "25px 10px",
                  color: "#40A9FF !important",
                }
          }
        >
          
        </Button>
        <Button
          onClick={() => {
            setShowCreate(true);
            setTile("");
          }}
          type="primary"
          style={
            isMobile
              ? {
                  margin: "5px 10px",
                }
              : {
                  margin: "25px 20px",
                }
          }
        >
          
        </Button>
      </div>
      <div
        className="disaccount-page-body"
        style={
          isMobile
            ? { backgroundColor: "white", margin: "5px" }
            : { backgroundColor: "white", margin: "0px 20px" }
        }
      >
        <Table
          columns={columns}
          dataSource={data && [...data].reverse()}
          rowKey={(record) => record}
          rowSelection={rowSelection}
          style={{ userSelect: "text" }}
          scroll={{ x: 800 }}
          loading={loading}
        />
      </div>
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    disaccount: state.form.disaccount,
  };
};
const actionCreator = { handleFetchDisaccount };
export default connect(mapStateToProps, actionCreator)(DisaccountPage);
